header{font-family:Helvetica,"Noto Sans KR","pretendard","맑은 고딕";max-width:1160px;margin:auto;height:20vh}header h1{font-family:"Noto Sans KR","pretendard","맑은 고딕",sans-serif;font-weight:bold;letter-spacing:1.1}header h1 a{font-size:29px}header .top{display:flex;justify-content:space-between}header .top nav ul{display:flex}header .top nav ul li{font-weight:bold;padding:0 15px}header .top nav ul li:nth-child(2){color:#333}header .top nav ul li:nth-child(3){color:#666}header .top nav ul li:nth-child(4){color:#999}header .top nav ul li:nth-child(5){color:#aaa}header .top nav ul li:nth-child(6){color:#bbb}header .top nav ul li:nth-child(7){color:rgba(0,0,0,.15)}header .top nav ul li:nth-child(8){color:rgba(0,0,0,.15)}